.zantimes-featured-post {
  padding-top: 60px;
  padding-bottom: 60px;
  overflow: hidden;
}
.zantimes-featured-post__post-details a:not(.btn-primary) {
  text-decoration: none;
  color: #1D0056;
}
.zantimes-featured-post__post-details .read-more a {
  margin-top: 15px;
}
.zantimes-featured-post__post-image img {
  width: 100%;
  border-radius: 8px;
}
.zantimes-featured-post:not(.text-color-light) h2 {
  color: #1D0056;
}
.zantimes-featured-post:not(.text-color-light) h3 {
  color: #000000;
}
.zantimes-featured-post.text-color-light h2, .zantimes-featured-post.text-color-light h3, .zantimes-featured-post.text-color-light p, .zantimes-featured-post.text-color-light a, .zantimes-featured-post.text-color-light .post-meta span {
  color: #FFFFFF;
}
@media screen and (max-width: 991px) {
  .zantimes-featured-post {
    padding-top: 30px;
    padding-bottom: 30px;
  }
  .zantimes-featured-post.reverse-mobile .zantimes-featured-post__post-image {
    order: -1;
  }
  .zantimes-featured-post__post-details {
    padding-top: 30px;
  }
}